.doctors_container {
    width: 100%;
}

.doctors_container .doctors_container_content {
    padding: 20px 50px;
}

.doctors_container .doctors_container_content .doctor {
    display: grid;
    grid-template-columns: 20fr 80fr;
    padding: 20px;
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    margin-bottom: 30px;
    border-radius: 5px;
    background-color: #fff;
}