.AppointmentList {
    position: relative;
    padding-top: 20px;
}

.AppointmentList .AppointmentListGrid {
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
    grid-gap: 30px;
}

.AppointmentList .AppointmentListGrid .AppointmentListGridItem {
    box-shadow: rgba(149, 157, 165, 0.2) 0px 8px 24px;
    background-color: #fff;
    padding: 15px;
    border-radius: 5px;
}