.BookAppointmentContainer {
    padding: 20px;
}

.BookAppointmentContainer .ContainerItem {
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    padding: 20px;
    border-radius: 5px;
    margin-bottom: 20px;
    background-color: #fff;
}

.BookAppointmentContainer .DoctorInfo {
    display: grid;
    grid-template-columns: 30fr 70fr;
}