.Home .section {
    text-align: center;
    padding: 30px 0;
    background-color: #fff;
    width: 90%;
    margin: 30px auto 0;
}

.Home .Header {
    background-image: url("https://ugm.ac.id/galleries/crop/14879--730x420px.jpg");
    background-size: cover;
    background-position: center;
    height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.Home .Header::before {
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background-color: rgb(0, 0, 0, .1);
    z-index: 0;
}

.Home .Header .HeaderContent {
    width: 60%;
    text-align: center;
    z-index: 1;
    /* color: #fff; */
}

.Home .Header .HeaderContent label,
.Home .Header .HeaderContent .demo-simple-select {
    /* color: #fff; */
    text-align: left;
}

.Home .Header .fields {
    display: grid;
    grid-template-columns: 30fr 70fr;
    grid-gap: 20px;
}

.Home .LookingForADoctor .Grid {
    display: grid;
    grid-template-columns: 50fr 50fr;
    grid-gap: 100px;
    padding: 20px 50px;
}

.Home .LookingForADoctor .Grid .GridItem .GridCard {
    display: flex;
    align-items: center;
    padding: 15px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 8px;
}

.Home .LookingForADoctor .Grid .GridItem .GridCard img {
    border-radius: 8px;
}

.Home .Specialists .Grid {
    display: grid;
    grid-template-columns: 16.66fr 16.66fr 16.66fr 16.66fr 16.66fr 16.66fr;
    grid-gap: 20px;
    padding-top: 20px;
}

.Home .Specialists .Grid .GridItem svg {
    font-size: 40px;
    background-color: #014e78;
    color: #ffffff;
    padding: 25px;
    border-radius: 50%;
}

.Home .ReviewsContainer .Grid {
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
    grid-gap: 20px;
    padding: 20px 50px;
}

.Home .ReviewsContainer .Grid .GridItem.ReviewCard {
    padding: 20px;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
    border-radius: 5px;
}