.OnLineVideoConsulting {
    background-color: #202124;
    padding: 20px;
}

.OnLineVideoConsulting .Grid {
    display: grid;
    grid-template-columns: 50fr 50fr;
    grid-gap: 20px;
    min-height: 70vh;
}

.OnLineVideoConsulting .Grid video {
    border-radius: 5px;
}

.OnLineVideoConsulting .Grid .LoadingScreen {
    width: 100%;
    min-height: 60vh;
    display: flex;
    align-items: center;
    justify-content: center;
    color: #fff;
    border: 1px solid #fff;
    border-radius: 5px;
}

.OnLineVideoConsulting .Grid .CallingControls.prev {
    width: 100%;
    display: grid;
    grid-template-columns: 33.33fr 33.33fr 33.33fr;
    align-items: center;
}

.OnLineVideoConsulting .Grid .CallingControls.current {
    display: flex;
    align-items: center;
    justify-content: center;
}

.OnLineVideoConsulting .Grid .CallingControls .ControlItem {
    color: #fff;
    text-align: center;
}